.App {
  text-align: center;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.michal-background {
  background-image: url("./assets/michal.jpg");
  background-size: cover;
  background-position: center top;
  min-height: 100vh;
}

.logo-name {
  font-family: "Lexend Mega", sans-serif;
  color: #fff;
  font-size: 60px;
  width: 20px;
}
.logo-name-pages {
  font-family: "Lexend Mega", sans-serif;
  color: #fff;
  font-size: 30px;
  text-align: center;
  margin: 50px 0 100px;
}
.logo-name-pages a {
  color: #fff;
  text-decoration: none;
}

.container {
  padding: 0 10%;
}

.navbar_link {
  display: block;
  font-size: 30px;
  text-decoration: none;
}

.navbar_link a {
  text-decoration: none;
  color: #fff;
}

.main_info {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
  min-height: 100vh;
}

