}
.post {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}
.post-mobile {
  margin: 0 auto;
}

.post-media {
  width: 480px;
  height: 270px;
  overflow: hidden;
  border-radius: 5px;
}

.post-media-mobile {
  width: 360px;
  height: 202.5px;
  overflow: hidden;
  border-radius: 5px;
  margin: 0 auto;
}

.post-media-image {
  width: 480px;
}

.post-media-image-mobile {
  width: 360px;
}

.post-text {
  width: 480px;
}

.post-text-mobile {
  max-width: 360px;
  margin: 20px auto;
  text-align: center;
  margin-bottom: 60px;
}

.text-left {
  text-align: left;
  margin-left: 50px;
}
.text-right {
  text-align: right;
  margin-right: 50px;
}

.mt-20 {
  padding-top: 100px;
}

.footer {
  background-color: #333333;
  color: gray;
  text-align: center;
  padding: 50px 0;
}

.youtube-icon,
.instagram-icon {
  font-size: 30px;
  margin-right: 5px;
}
.instagram-icon {
  color: #dd2a7b;
}
.youtube-icon {
  color: #ff0000;
}
.copyright {
  margin-top: 10px;
  display: block;
}

.icons-contact {
  margin-top: 10px;
}

