  color: #fff;
  background-attachment: fixed;
  padding-bottom: 50px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.animate-title {
  animation: slide-in 1.5s ease-out;
}

@keyframes slide-in {
  0% {
    transform: translateX(-600%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
